body {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  font-weight: 400;
  background: #F7FAFC;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

button,
input,
textarea,
select {
  outline: none !important;
  box-shadow: none !important;
}

.btn-primary {
  font-size: 13px;
  font-weight: 500;
  padding: 10px 20px;
  min-width: 130px;
  background: #00376A !important;
  border: 1px solid #00376A !important;
  border-radius: 4px;
}

.gradientBg {
  background: linear-gradient(to bottom, #2f75b5 0%, #00376a 100%) !important;
  border: none !important;
}

.btn-oreang {
  font-size: 13px;
  font-weight: 500;
  padding: 10px 20px;
  min-width: 130px;
  background: #F47320 !important;
  border: 1px solid #F47320 !important;
  color: #fff !important;
  border-radius: 4px;
}


.btn-org-gradient {
  font-size: 13px;
  font-weight: 500;
  padding: 10px 20px;
  min-width: 130px;
  background: transparent linear-gradient(180deg, #ECAC83 0%, #F47320 100%) 0% 0% no-repeat padding-box;
  color: #fff !important;
  border: none;
  border-radius: 4px;
  text-align: center;
}

.btn-secondary {
