.menuList {
  height: calc(100% - 55px);
  overflow: hidden;
  overflow-y: auto;
}

.menuList>li {
  display: block;
  padding: 15px 15px;
}

.menuList>li a {
  display: flex;
  align-items: center;
  color: #B0B0B0;
  font-size: 13px;
  white-space: nowrap;
}

.menuList>li a.active img,
.menuList>li a:hover img {
  filter: inherit;
}

.menuList>li a img {
  filter: grayscale(1);
}

.collapseTgl {
  position: relative;
  padding-right: 30px;
}

.menuList>li a.active,
.menuList>li a:hover,
.collapseTgl[aria-expanded="true"] {
  color: #F47320;
}

.collapseTgl:after {
  content: "\F282";
  font-family: bootstrap-icons !important;
  position: absolute;
  top: 0;
  right: 0;
}

.collapseTgl[aria-expanded="true"]:after {
  transform: rotate(-180deg);
}

