.toggleSwitch input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
}
.toggleSwitch label {
  cursor: pointer;
  text-indent: -9999px;
  width: 50px;
  height: 24px;
  background: #dcdcdc;
  display: block;
  border-radius: 100px;
  position: relative;
}
.toggleSwitch label:after {
  content: "";
  position: absolute;
  top: 4px;
  left: 4px;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 90px;
  transition: .3s;
}
.toggleSwitch input:checked+label {
  background: #B2C2D2;
}
.toggleSwitch input:checked+label:after {
  left: calc(100% - 4px);
  transform: translateX(-100%);
  background: #00376A;
}
